.footer{
    position: fixed;
    width: 100%;
    bottom: 0;
   
    
}
@media only screen and (max-width: 1100px) {
.footer__icons{
    font-size:10px;
}
}
.header{
    position:fixed;
    /* background: blue; */
    top:0;
    width: 100%;
    /* margin-top: 13px; */
    max-height: 8vh;
    text-transform: uppercase;
    z-index:100;
}

.header__Burger{
    display: none;
    margin: 0 30px;
 }

 .header__Links:hover{
 border-bottom: 1px solid white;

 }

@media only screen and (max-width: 1084px){
    .header__Burger{
       display: block;
       /* margin-top:40px; */
       
    }
    
     .header__Link{
        
        position: absolute;
        margin-top:30px;
        top:0;
        left:0;
        height:100vh;
        box-shadow: 10px 10px 10px rgba(26,54,68,0.03);
        background: rgba(255,255,255, 0.2);
        -webkit-backdrop-filter: blur(10px);
                backdrop-filter: blur(10px);
        border: solid 2px transparent;
        width:50%;
        padding:40px;
        transform: translate(-500px);
        transition: 1.0s ease-in-out;
     }

    .header__LinkList{
        align-items: center;
        flex-direction: column;
        display: block;     
        
    }
    .header__Links:hover{
        border-bottom: none;      
        }
    
    .header__Links{
        padding:20px;
        
    }
    
    .header__BodyLogo {
      font-size: 20px;
      /* margin-top:-13px !important; */

    }
    

}

@media only screen and (max-width: 400px){
   .header__Link{
       width:100%;
   }

}


